export default class Tetris {
  constructor(canvas) {
    this.ctx = canvas.getContext("2d");
    this.scaleFactor = 50
    this.rows = 20
    this.columns = 10
    this.gameBoard = []
    for(let i  = ['E']; this.gameBoard.length < this.columns; this.gameBoard.push(i));

  }
  drawRect(x,y, colour) {
    const scaledX = x * this.scaleFactor
    const scaledY = y * this.scaleFactor
    this.ctx.fillStyle = colour;
    this.ctx.fillRect(scaledX,scaledY,scaleFactor,scaleFactor);

    this.ctx.strokeStyle = "white";
    this.ctx.lineWidth   = 0.5;
    this.ctx.strokeRect(scaledX,scaledY,scaleFactor,scaleFactor);
  }

  run()
  {
    console.log(this.gameBoard[20][0]);

  }

}



